.app-download {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  .download {
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: 20px;
    color: white;
    padding: 10px 25px;
    background-color: #363B3F;
    text-decoration: none;
    border-radius: 15px;

    &:hover {
      background-color: #2E3437;
    }

    svg {
      height: 30px;
      padding-right: 10px;
    }
  }
}