.app-header {
  background-color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 300px;

  .app-logo {
    user-select: none;
    display: flex;
    flex-direction: row;
    align-items: center;

    .app-logo-img {
      flex: 0 0 60px;
      padding: 10px 20px;
    }

    .app-logo-name {
      user-select: none;
      @import url('https://fonts.googleapis.com/css?family=Ultra&display=swap');
      font-family: 'Ultra', serif;
      //font-family: serif;
      font-size: 40px;
      //font-weight: bolder;
    }
  }

  .app-actions {
    padding: 20px;
    flex: 1;
    display: flex;
    justify-content: flex-end;
    a {
      padding: 20px;
      color: #000;
      text-decoration: none;

      &:hover {
        color: #00CAF4;
      }
    }
  }
}
