@import url(https://fonts.googleapis.com/css?family=Ultra&display=swap);
@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app-header{background-color:white;display:flex;flex-direction:row;justify-content:space-between;padding:0 300px}.app-header .app-logo{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;display:flex;flex-direction:row;align-items:center}.app-header .app-logo .app-logo-img{flex:0 0 60px;padding:10px 20px}.app-header .app-logo .app-logo-name{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;font-family:'Ultra', serif;font-size:40px}.app-header .app-actions{padding:20px;flex:1 1;display:flex;justify-content:flex-end}.app-header .app-actions a{padding:20px;color:#000;text-decoration:none}.app-header .app-actions a:hover{color:#00CAF4}.app-body{flex:1 1}.app-section{color:white;display:flex;flex-direction:row;justify-content:center}.app-section .app-section-description{padding:60px;display:flex;flex-direction:column;justify-content:center}.app-section .app-section-description .app-section-description-title{font-family:"Source Sans Pro", Helvetica Neue, -apple-system, BlinkMacSystemFont, Helvetica, sans-serif}.app-section .app-section-description .app-section-description-extra{padding:30px;font-family:"Source Sans Pro", Helvetica Neue, -apple-system, BlinkMacSystemFont, Helvetica, sans-serif}.app-section .app-section-image{margin:60px;border-radius:10px;box-sizing:border-box}.app-image-container{position:relative;display:flex;flex-direction:column;align-items:center}.app-download{display:flex;flex-direction:row;justify-content:center;align-items:center}.app-download .download{display:flex;flex-direction:row;align-items:center;font-size:20px;color:white;padding:10px 25px;background-color:#363B3F;text-decoration:none;border-radius:15px}.app-download .download:hover{background-color:#2E3437}.app-download .download svg{height:30px;padding-right:10px}.App{height:100%;text-align:center;display:flex;flex-direction:column}html{background-color:#76B59B}

